<template>
  <div class="wrapper"  ref="wrapper" 
  style="position:absolute;top:21px;bottom:0px;width:100%;overflow:hidden;">
  <ul class="content">
    <li v-for="item in 100" :key="item">
      {{item}}
    </li>
  </ul>
</div>
</template>
<script>
import BScroll from "better-scroll";

export default {
  name: "test1",
  data() {
    return {
      scroll: ""
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {
        click: true
      });
    });
  }
};
</script>



